.header {
    width: 100%;
    display: flex;
    align-items: center;
    position: fixed;
    top: 0;
    z-index: 999;
    box-shadow: 0px 5px 8px -9px rgba(0,0,0,0.75);
    background-color: #fff;
    padding: 15px 20px;


    /* height: 50px; */
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.widget__options{
    width: 100%;
    height: auto;
    box-shadow: 0px 5px 8px -9px rgb(0 0 0 / 75%);
    background: var(--primary-color);
    position: fixed;
    z-index: 999;
    padding: 7px;
    display: flex;
}
.header__left {
    flex: 3;
}
.profile  {
    width: 80px;
    height: 80px;
    background-color: #1fbc85;
    border-radius: 50%;
    text-align: center;
    padding: 15% 0;
    margin: 0 5px;
}

.profile > div {
    font-family: 'Manrope';
    font-size: 36px;
    color: white;
}
.header__center {
    flex: 5;
    display: flex;
    align-items: center;
}
.header__right {
    display: flex;
    justify-content: space-between;
}
.header__option > IconButton {
    margin: 0 10px;
}
.font-9 {
    font-size: 9px;
}

.title-case {
    text-transform: capitalize;
}
.header__option:hover{
    color: red;
}
.header__center>input {
    border: none;
    background: transparent;
    outline-width: 0;
    padding: 12px;
    width: 100%;
}

.logo-header {
        width: 140px !important;
}
.page-form::placeholder{
    margin-left: 30px;
}
.header__info {
    display: flex;
    align-items: center;
    margin-left: 30px;
}
.header__info > h4 {
    margin-left: 10px;
    font-family: var(--primary-font)!important;
    font-size: 12px;
    font-weight: 700;
}

.sidebar{
    width: 6%;
    height: 100vh;
    background-color: var(--primary-color);
    z-index: 5;
    position: fixed;
    top: 10%;
    overflow-y: scroll;

}
.sidebar__title {
    font-size: 9px;
    text-align: center;
}

.sidebarRow {
    display: block;
    padding: 10px;
    cursor: pointer;
    color: white;
    padding: 25px 0; 
    text-align: center;
}
.sidebarRow:hover{
    background-color: #202236;
}

.sidebarRow > h4 {
    margin-left: 20px;
    color: #2e41f4;
}

.logo_w {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .logo_w {
      animation: App-logo-spin infinite 20s linear;
    }
  }